import { ViewModelContext } from '@farris/mobile-devkit';
import { ComponentExtender } from './component-extender';

/**
 * 列表组件扩展
 */
class ListviewExtender implements ComponentExtender {

  private listviewInstance: any;

  private viewModelContext: ViewModelContext;

  constructor(listviewInstance: any, viewModelContext: any) {
    this.listviewInstance = listviewInstance;
    this.viewModelContext = viewModelContext;
  }

  /**
   * 扩展组件
   */
  extend() {
    const self = this;
    this.viewModelContext.appContext.eventBus.subscribe(`${this.viewModelContext.id}-listview-extend`, (value) => {
      if (value.type === 'listviewFinished') {
        this.viewModelContext.uiState.setPropertyValue('listaviewState', { ...this.viewModelContext.uiState['listaviewState'], loadMoreFinished: true });
      }
      if (value.type === 'listviewUnFinished') {
        this.viewModelContext.uiState.setPropertyValue('listaviewState', { ...this.viewModelContext.uiState['listaviewState'], loadMoreFinished: false });
      }
      if (value.type === 'loadDataFinished') {
        this.viewModelContext.uiState.setPropertyValue('listaviewState', { ...this.viewModelContext.uiState['listaviewState'], refreshLoading: false, loadMoreLoading: false });
      }
    })
    // 当前行
    this.listviewInstance.$on('selectChange', (selectedData) => {
      const selectedId = selectedData.data.id;
      self.viewModelContext.bindingData.getList().setCurrentId(selectedId, true, true);
    });

    // 批量选择
    this.listviewInstance.$on('itemClick', () => {
      const selectedItems = self.listviewInstance.getSelectDataList();
      let selectedIds = [];
      if (selectedItems && selectedItems.length > 0) {
        selectedIds = selectedItems.map((selectedItem) => {
          return selectedItem.id;
        });
      }
      self.viewModelContext.uiState.setPropertyValue('selectedIds', selectedIds);
    });


    // 监听下拉刷新组件
    this.listviewInstance.$on('refresh', () => {
      self.viewModelContext.repository.setPaginationInfo({
        pageIndex: 1
      })
      self.viewModelContext.uiState.setPropertyValue('listaviewState', { ...self.viewModelContext.uiState['listaviewState'], refreshLoading: true, loadMoreLoading: true })
      self.viewModelContext.appContext.eventBus.triggerEvent({ type: 'LoadData' })
    });

    // 监听是否走查询
    this.listviewInstance.$on('load', () => {
      const loading = self.viewModelContext.uiState['listaviewState']['loadMoreLoading'];
      if (!loading) {
        self.viewModelContext.uiState.setPropertyValue('listaviewState', { ...self.viewModelContext.uiState['listaviewState'], loadMoreLoading: true });
        let pageIndex = self.viewModelContext.repository.paginationInfo && self.viewModelContext.repository.paginationInfo.pageIndex;
        if (!pageIndex) {
          pageIndex = 0;
        }
        self.viewModelContext.repository.setPaginationInfo({
          pageIndex: pageIndex += 1
        })
        self.viewModelContext.appContext.eventBus.triggerEvent({ type: 'LoadData' })
      }
    });
  }

  /**
   * 清理状态
   */
  dispose() {
    this.listviewInstance.$off('selectChange');
    this.listviewInstance.$off('itemClick');
    this.listviewInstance.$off('load');
    this.viewModelContext.appContext.eventBus.unsubscribe(`${this.viewModelContext.id}-listview-extend`);
  }

}

export { ListviewExtender }