import { Subject } from 'rxjs';
import { MapOperation } from './map-operation.interface';

export abstract class MapService {
    // 地图操作接口
    public locationInstance: MapOperation;

    public location$ = new Subject();
    /**
     * 获取当前位置
     */
    getCurrentLocation() {
        this.locationInstance.getCurrentLoc();
    }

    /**
     * 获取坐标集合
     */
    getMapMarkers() {
        return this.location$;
    }

    setMapMarkers() {
        // this.locationInstance.location$.subscribe((res) => {
        //     this.markers.next(res);
        // });
    }
}
