import { ViewModelContext } from '@farris/mobile-devkit';
import { ComponentExtender } from './component-extender';

/**
 * 列表组件扩展
 */
class AttachmentExtender implements ComponentExtender {

    private attachmentInstance: any;

    private viewModelContext: ViewModelContext;

    constructor(attachmentInstance: any, viewModelContext: any) {
        this.attachmentInstance = attachmentInstance;
        this.viewModelContext = viewModelContext;
    }

    /**
     * 扩展组件
     */
    extend() {
        const self = this;
        // 当前行
        this.attachmentInstance.$on('beforeSelect', (selectedData) => {
            const selectedId = selectedData.id;
            self.viewModelContext.bindingData.getList().setCurrentId(selectedId, true, false);
        });
    }

    /**
     * 清理状态
     */
    dispose() {
        this.attachmentInstance.$off('beforeSelect');
    }

}

export { AttachmentExtender };
