import { ViewModelContext } from '@farris/mobile-devkit';
import { ComponentExtender } from './component-extender';

/**
 * 列表组件扩展
 */
class SearchExtender implements ComponentExtender {

  private searchInstance: any;

  private viewModelContext: ViewModelContext;

  constructor(searchInstance: any, viewModelContext: any) {
    this.searchInstance = searchInstance;
    this.viewModelContext = viewModelContext;
  }

  /**
   * 扩展组件
   */
  extend() {
    const self = this;
    // 监听搜索组件
    this.searchInstance.$on('cancel', (data) => {
      //  清空走查询
      self.viewModelContext.repository.setPaginationInfo({
        pageIndex: 1
      })
      self.viewModelContext.appContext.eventBus.triggerEvent({ type: 'LoadData' })
    });
    this.searchInstance.$on('search', (data) => {
        // 如果是确定类型，即走查询
        self.viewModelContext.repository.setPaginationInfo({
          pageIndex: 1
        })
        self.viewModelContext.appContext.eventBus.triggerEvent({ type: 'LoadData' })
    });
  }

  /**
   * 清理状态
   */
  dispose() {
    this.searchInstance.$off('cancel');
    this.searchInstance.$off('search');
  }

}

export { SearchExtender }