
const bindSearchExtenderDir = {

  mounted: function(el, binding, vnode, oldVnode) {
    const componentInstance = vnode.children[0].children[0].componentInstance as any;
    componentInstance.$bindComponentExtender(componentInstance, 'Search');
  },

  unmounted: function(el, binding, vnode, oldVnode) {
    const componentInstance = vnode.children[0].children[0].componentInstance as any;
    componentInstance.$unbindComponentExtender(componentInstance);
  }
};

export { bindSearchExtenderDir };

