import { Subject } from 'rxjs';
import { MarkerLocation } from './location.interface';
import { MapOperation } from './map-operation.interface';
export class CloudPlusMapOperation implements MapOperation {

    constructor(private location$: Subject<any>) {
    }
    /**
     * 云上协同  调用获取当前位置接口
     */
    getCurrentLoc() {
        // 具体实现
        if (window['iCityBridge']) {
            const iCityBridge = window['iCityBridge'];
            iCityBridge.ccworkGetLocation((result: any) => {
                alert(JSON.stringify(result));
                // 结果预处理

                // 发布位置结果
                this.location$.next(this.assembleMarkers(result));
            });
        }
    }

    // 获取定位回调
    private locationCallback(result?: any) {
        alert(JSON.stringify(result));
        // 结果预处理

        // 发布位置结果
        this.location$.next(this.assembleMarkers(result));
    }


    /**
     * 组装坐标结果
     * @param res 结果
     */
    private assembleMarkers(res) {
        const loc: MarkerLocation = res.result;
        return [{ position: [Number(loc.lng), Number(loc.lat)], visible: true, draggable: true }];
    }
}
