import { WeChatTakePhoto } from './wechat-take-photo.service';
import { AttachmentService } from './attachment.service';
import { CloudPlusTakePhoto } from './cloud-plus-take-photo.service';

export class WeChatAttachment extends AttachmentService {
    constructor() {
        super();
        this.takePhotoIns = new WeChatTakePhoto();
    }
}

export class CloudPlusAttachment extends AttachmentService {
    constructor() {
        super();
        this.takePhotoIns = new CloudPlusTakePhoto(this.collectFile);
    }
}
