import { BindingValueConverter } from '@farris/mobile-devkit';

/**
 * 字符串数组转换器
 */
class StringArrayConverter implements BindingValueConverter {

  constructor(private separator: string = ',') {
  }

  /**
   * 控件值（字符串数组）=>数据源（字符串）
   */
  convertFrom(value: string[]): string {
    return value.join(this.separator);
  }

  /**
   * 数据源（字符串）=> 控件值（字符串数组）
   */
  convertTo(value: string): string[] {
    return value.split(this.separator);
  }
}

export { StringArrayConverter };
