import { BefRepository } from '@farris/mobile-bef';
import { HttpMethods } from '@farris/mobile-devkit';
import { Observable, forkJoin } from 'rxjs';
import { filter, map, mergeMap, tap } from 'rxjs/operators';
import { getToken, mergeString } from '../utils';

export class UploadAttachmentService {
    constructor(private befRepository: BefRepository<any>) {

    }

    /**
     * 缓存附件
     * @param originFiles
     * @param uploadInfo
     */
    upload(originFiles: any[], uploadInfo: any) {
        const apiProxy = this.befRepository.apiProxy;
        const uploadUrl = `/api/runtime/dfs/v1.0/formdoc`;
        const parametersArr = originFiles.map(ele => {
            return {
                docInfo: {
                    fileContent: ele.content.substring(
                        ele.content.indexOf(',') + 1
                    ),
                    fileName: ele.file.name,
                    index: 0,
                    total: 1,
                    metadataId: ele.id
                },
                formId: uploadInfo.parentDirName,
                mode: 1,
                rootId: uploadInfo.rootId || 'default-root'
            };
        });
        const requestList = parametersArr.map(ele => {
            const params = { body: ele };
            return apiProxy.request(HttpMethods.POST, uploadUrl, params, true);
        });
        return forkJoin(requestList);
    }

    /**
     * 预览附件pdf
     * @param fileType
     * @param rootId
     * @param metadataIdList
     */
    previewPdf(rootId: string, metadataIdList: string[]) {
        const baseUrl = '/platform/runtime/dfs/web/pdfjs/viewer.html?file=';
        const apiProxy = this.befRepository.apiProxy;
        const fileListUrl = '/api/runtime/dfs/v1.0/fileviewer/filelist';
        const params = {
            storageType: 2, filePath: JSON.stringify({
                rootId,
                metadataIdList
            })
        };
        const body = { body: params };
        // 预览
        return apiProxy.request(HttpMethods.POST, fileListUrl, body).pipe(
            map(res => baseUrl + encodeURIComponent(res[0].filePath))
        );
    }

    /**
     * 预览office txt文档
     * @param rootId
     * @param metadataIdList
     */
    previewDoc(rootId: string, metadataIdList: string[]) {
        const apiProxy = this.befRepository.apiProxy;
        const fileListUrl = '/api/runtime/dfs/v1.0/fileviewer/filelist';
        const loadDocUrl = '/api/runtime/dfs/v1.0/docviewer/loaddocument';
        const params = {
            storageType: 2, filePath: JSON.stringify({
                rootId,
                metadataIdList
            })
        };
        const body = { body: params };
        // 根据文件类型 调用不同的预览接口
        return apiProxy.request(HttpMethods.POST, fileListUrl, body)
            .pipe(
                mergeMap(res => apiProxy.request(HttpMethods.POST, loadDocUrl, { body: { filePath: res[0].filePath } })),
                map(res => res.pages)
            );
    }


    /**
     * 获取图片url
     * @param attachmentId  附件id
     * @param rootId 根目录id
     * @returns 
     */
    getAttachmentPath(attachmentId: string, rootId: string) {
        return `/api/runtime/dfs/v1.0/doc/filecontent?metadataid=${attachmentId}&rootid=${rootId}&token=${getToken(mergeString(attachmentId, rootId))}`;
    }
}
