import { BindingValueConverter, DateUtil } from '@farris/mobile-devkit';

/**
 * 日期转换器
 */
class DateConverter implements BindingValueConverter {

  /**
   * 构造函数
   * @param format 日期格式
   */
  constructor(private format: string) {
  }

  /**
   * 控件值（Date对象）=>数据源（字符串）
   */
  convertFrom(value: Date): string {
    if (!value) {
      return '0001-01-01T00:00:00';
    }

    const format = this.format ? this.format : 'yyyy-MM-dd HH:mm:ss';
    return DateUtil.format(value, format);
  }

  /**
   * 数据源（字符串）=> 控件值（Date对象）
   */
  convertTo(value: string): Date {
    if (DateUtil.isEmptyDateString(value) === true) {
      return null;
    }
    return DateUtil.parse(value);
  }

}
export { DateConverter };
