import { ViewModel, ViewModelContext } from '@farris/mobile-devkit';
import { ComponentExtender } from './component-extender';
import { ComponentExtenderFactory } from './component-extender-factory';

/**
 * 扩展管理器
 */
class ComponentExtenderManager {

  private viewModelContext: ViewModelContext;

  /**
   * 组件扩展器集合
   */
  private extenderMap: Map<any, ComponentExtender>;

  /**
   * 扩展器工厂
   */
  private extenderFactory: ComponentExtenderFactory;

  /**
   * 构造函数
   */
  constructor(viewModelContext: ViewModelContext) {
    this.viewModelContext = viewModelContext;
    this.extenderMap = new Map<any, ComponentExtender>();
    this.extenderFactory = new ComponentExtenderFactory(viewModelContext);
  }

  /**
   * 获取扩展
   */
  public getExtender(componentInstance: any, componentType: string) {
    if (this.extenderMap.has(componentInstance)) {
      return this.extenderMap.get(componentInstance);
    }

    const extender = this.extenderFactory.create(componentInstance, componentType);
    this.extenderMap.set(componentInstance, extender);

    return extender;
  }
}
export { ComponentExtenderManager };
