import { Subject } from 'rxjs';
import { TakePhoto } from './take-photo.interface';
export abstract class AttachmentService {
    public takePhotoIns: TakePhoto;
    public collectFile = new Subject();
    constructor() {
    }

    /**
     * 拍照
     * @param param 拍照参数
     */
    public takePhoto(param?: any) {
        this.takePhotoIns.takePhotoAttachment(param);
    }

    /**
     * 选择文件
     * @param param 选择文件参数
     */
    public selectAttachment(param: any) {
        this.takePhotoIns.selectAttachments(param);
    }

    /**
     * 拍摄短视频
     * @param param 拍摄短视频参数
     */
    public takeShortVideo(param: any) {
        this.takePhotoIns.takeShortVideo(param);
    }

    /**
     * 播放短视频
     * @param param 播放短视频参数
     */
    public playShortVideo(param: any) {
        this.takePhotoIns.playShortVideo(param);
    }

    public selectFile(param: any) {
        this.takePhotoIns.selectFile(param);
    }
    public downloadAttachment(param: any) {
        this.takePhotoIns.downloadAttachment(param);
    }

    public getSysInfo(){
        return this.takePhotoIns.getSysInfo();
    }
}
