import { ViewModel } from '@farris/mobile-devkit';

/**
 * 映射命令方法
 */
function mapCommandMethods(methodNames: any) {

  const mappedCommandMethods = {};
  methodNames.forEach((methodName: string) => {
    mappedCommandMethods[methodName] = function (eventParams: any) {
      return this.$viewModel[methodName](eventParams);
    };
  });

  return mappedCommandMethods;
}

export { mapCommandMethods }