import { ViewModelContext } from '@farris/mobile-devkit';
import { ComponentExtender } from './component-extender';

/**
 * 列表组件扩展
 */
class MapExtender implements ComponentExtender {

    private mapInstance: any;

    private viewModelContext: ViewModelContext;

    constructor(mapInstance: any, viewModelContext: any) {
        this.mapInstance = mapInstance;
        this.viewModelContext = viewModelContext;
    }

    /**
     * 扩展组件
     */
    extend() {
        const self = this;
        // 当前行
        this.mapInstance.$on('markMap', (selectedData) => {
            // 获取经纬度
            const lnglat = selectedData.lnglat;
            const lng = lnglat.lng;
            const lat = lnglat.lat;
            // 获取经纬度路径
            const lngPath = this.mapInstance.lngPath;
            const latPath = this.mapInstance.latPath;
            // this.mapInstance.center = [lng, lat];
            // 赋值经纬度
            this.viewModelContext.bindingData.setValue(
                this.getBindingPathArray().concat(lngPath.split('/')),
                lng,
                true,
                true);
            this.viewModelContext.bindingData.setValue(
                this.getBindingPathArray().concat(latPath.split('/')),
                lat,
                true,
                true
            );
        });
    }

    /**
     * 清理状态
     */
    dispose() {
        this.mapInstance.$off('markMap');
    }

    private getBindingPathArray(): any[] {
        const path = this.viewModelContext.bindingData.bindingPath;
        if (path) {
            return path.split('/').filter((n) => n !== '');
        }
        return [];
    }
}

export { MapExtender };
